const gallery = document.querySelector('.gallery')

const createGalleryItem = ({preview, original, description}) =>
  `<li class="gallery-item">
	<a class="gallery-link gallery-link-js" href="${original}">
		<img
		  class="gallery-image"
		  src="${preview}"
		  alt="${description}"
		/>
	</a>
</li>
`

const allGalleryItems = images.map(item => createGalleryItem(item)).join(' ')

gallery.innerHTML = allGalleryItems

new SimpleLightbox('.gallery-link-js', {captionsData: 'alt', captionDelay: 500, overlayOpacity:0.8, navText: ['<svg width ="11" height ="20"><use href="css/symbol-defs.svg#icon-arrow-right"></use></svg>','<svg width ="11" height ="20"><use href="css/symbol-defs.svg#icon-arrow-left"></use></svg>' ], closeText:'<svg class ="icon-close" width ="14" height ="14"><use href="css/symbol-defs.svg#icon-close"></use></svg>'});


